import React from 'react';
import PropTypes from 'prop-types';

class Modal extends React.Component {
  render() {
    // Render nothing if the "show" prop is false
    if(!this.props.show) {
      return null;
    }

    // The gray background
    const backdropStyle = {
      position: 'fixed',
      top: 0,
      bottom: 0,
      left: 0,
      right: 0,
      backgroundColor: 'rgba(0,0,0,0.5)',
      padding: 50
    };

    // The modal "window"
    const modalStyle = {
      backgroundColor: '#fff',
      borderRadius: 5,
      maxWidth: 1000,
      minHeight: 600,
      margin: '0 auto',
      padding: 30
    };

    return (
      <div>
      <div className="backdropz" style={backdropStyle}>
        <div className="modalz" style={modalStyle}>
        <button className="pull-right" onClick={this.props.onClose}>
              X
            </button>
          {this.props.children}

          <div className="footerz">

          </div>
        </div>
      </div>
      </div>
    );
  }
}

Modal.propTypes = {
  onClose: PropTypes.func.isRequired,
  show: PropTypes.bool,
  children: PropTypes.node
};

export default Modal;



